type GenericObject = Record<string, unknown> | Array<unknown> | string | number | boolean | RegExp

function isObject(obj: unknown): obj is Record<string, GenericObject> {
  return obj !== null && typeof obj === "object"
}

function isArray(obj: unknown): obj is Array<GenericObject> {
  return obj !== null && Array.isArray(obj)
}

function isString(obj: GenericObject): obj is string {
  return typeof obj === "string"
}

export type { GenericObject }
export { isObject, isArray, isString }
